@import '../../style/Media.scss';

.todoItem {
    max-width: 100%;
    width: 100%;
    
    margin-right: 24px;
    margin-top: 24px;

    text-align: start;

    &[data-is-complete="true"] {
        opacity: 0.5;
    }

    @include media("tablet") {
        width: auto;
    }
}